.not-found {
    min-height: 78vh;
    display: grid;
    gap: 2rem;
    grid-template-rows: 1fr 1fr;
    align-items: end;
    align-content: space-between;
    
    
    
    @media (min-width: $breakpoint-md) {
        grid-template-rows: 1fr auto;
        align-items: start;        
    }

    @media (min-width: $breakpoint-lg) {
        padding: 0 8rem;
        grid-template-columns: auto 1fr;
        grid-template-rows: 1fr;
        align-items: center;
        gap: 4rem;
    
        div {
            grid-column: 1/2;
            grid-row: 1/2;
        }
}
}