
// Font Family

$ff-base: 'Inter';

// Font Sizes

$fs-400: clamp(1rem, 1vw + 0.5rem, 1.25rem);
$fs-600: clamp(1.25rem, 1vw + 1.25rem, 2rem);
$fs-700: clamp(1.728rem, 2vw + 1rem, 2.441rem);
$fs-800: clamp(2.125rem, 2vw + 1.5rem, 3.052rem);
$fs-900: clamp(2.125rem, 3vw + 1.5rem, 3.815rem);

$fs-heading: $fs-900;
$fs-title: $fs-800;
$fs-subtitle: $fs-700;
$fs-label: $fs-600;
$fs-body: $fs-400;

// Font weights

$fw-regular: 400;
$fw-bold: 700;

// Classes

body {
    font-family: $ff-base, sans-serif;
    font-size: $fs-400;
}

.heading {
    font-size: $fs-heading;
    font-weight: $fw-bold;
    letter-spacing: -0.5px;
    line-height: 1.1;
}

.title {
    font-size: $fs-title;
    font-weight: $fw-bold;
    line-height: 1.1;
}

.subtitle {
    font-size: $fs-subtitle;
    font-weight: $fw-bold;
}

.label {
    font-size: $fs-label;
    font-weight: $fw-bold;
}

.paragraph {
    font-size: $fs-body;
    font-weight: $fw-regular;
}


@media (max-width: 264px) {
    .heading {
        font-size: 32px;
    }

    .title {
        font-size: 28px;
    }

    .subtitle {
        font-size: 23px;
    }
    
}