.content-grid, .full-width {
    $max-content-width: 23rem;
    $padding: 1.5rem;


    display: grid;
    grid-template-columns: [full-width-start] minmax($padding, 1fr) [content-start] min(100% - ($padding * 2), $max-content-width) [content-end] minmax($padding, 1fr) [full-width-end];

    @media (min-width: $breakpoint-md) {
        $max-content-width: 31rem;
        grid-template-columns: [full-width-start] minmax($padding, 1fr) [content-start] min(100% - ($padding * 2), $max-content-width) [content-end] minmax($padding, 1fr) [full-width-end];
    }

    @media (min-width: $breakpoint-lg) {
        $max-content-width: 70rem;
        $padding: 2rem;
        grid-template-columns: [full-width-start] minmax($padding, 1fr) [content-start] min(100% - ($padding * 2), $max-content-width) [content-end] minmax($padding, 1fr) [full-width-end];
    }

    @media (max-width: 300px) {
        $padding: 1rem;
        grid-template-columns: [full-width-start] minmax($padding, 1fr) [content-start] min(100% - ($padding * 2), $max-content-width) [content-end] minmax($padding, 1fr) [full-width-end];
    }
}

.content-grid > * , .full-width > * {
    grid-column: content;
}

.full-width {
    grid-column: full-width;
}