.case-study-hero {
    padding: 2rem 0 6rem 0;

    @media (max-width: $breakpoint-md) {
        padding-top: 1rem;
    }


    .intro {
        &__img {
            max-width: 50%;
            justify-self: center;
        }
        
        @media (min-width: $breakpoint-lg) {
            align-items: center;
            gap: 0;
        }
        
        @media (max-width: $breakpoint-lg) {
            &__img {
                display: none;
            }

            & {
                padding-top: 0;
            }
        }
    }

}


.overview {
    display: grid;
    gap: 2rem;

    @media (min-width: $breakpoint-md) {
        grid-template-columns: 1fr 1fr;
    }

    @media (min-width: $breakpoint-lg) {
        grid-template-columns: 1fr 1fr 1fr;
    }

}


.overview-item {
    display: flex;
    gap: 1rem;
    
    .icon {
        background-color: $clr-neutral-900;
        border-radius: 8px;
        display: grid;
        place-items: center;
        min-width: 64px;
        aspect-ratio: 1;
        padding: 1rem;
        
        img {
            max-height: 24px;
        }
    }

    .content {
        display: flex;
        flex-direction: column;
        gap: .5rem;
    }


    @media (max-width: 264px) {
        .icon {
            display: none;
        }
        
    }
}