.hero {
    min-height: 74vh;
    align-content: center;
    
    @media (min-width: $breakpoint-lg) {
        grid-template-columns: 1fr 1fr;
        align-items: center;
    }

    @media (min-width: $breakpoint-lg) and (max-width: 1080px) {
        .heading {
            font-size: 49px;
        }
        
    }

    margin-bottom: 7.5rem;
}

.hero__img {
    max-width: min(100%, 25rem);
    justify-self: center;

    @media (min-width: $breakpoint-lg) {
        max-width: 100%;
    }
}


.hero__content {
    @media (min-width: $breakpoint-lg) {
        grid-column: 1 / 2;
        grid-row: 1 / 2;
    }

    @media (min-width: $breakpoint-lg) {
        
        .paragraph {
            max-width: 34ch;
        }
        
    }

    @media (min-width: 300px) and (max-width: 355px) {
        .label {
            max-width: 15ch;
        }
    }

    @media (min-width: 285px) and (max-width: 355px) {
        .heading {
            max-width: 11ch;
        } 
    }

    @media (min-width: 655px) and (max-width: 796px) {
        .heading {
            max-width: 11ch;
        } 
    }
}
