.btn {
    text-transform: uppercase;
    font-weight: $fw-bold;
    color: $clr-neutral-900;
    border: 2px solid $clr-neutral-900;
    padding: 1em 2rem;
    border-radius: 8px;
    width: 100%;
    align-self: start;

    transition: all 200ms ease-in-out;
}

.btn:hover {
    scale: 0.95;
    background-color: $clr-neutral-900;
    color: $clr-neutral-100;
}

@media (min-width: $breakpoint-lg) {
    .btn {
        width:  auto;
        padding: 0.8rem 2rem;
    }
}

.btn-with-icon {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: .8rem;

    svg {
        max-width: 24px;
        height: auto;
    }

    .icon path {
        fill: $clr-neutral-900;
        transition: fill 200ms ease-in;
    }

    &:hover .icon path {
        fill: $clr-neutral-100;
    }

    .icon-with-stroke {
        fill: $clr-neutral-100;
        stroke: $clr-neutral-900;
        stroke-width: 50;
        transition: stroke 200ms ease-in-out,
                    fill 200ms ease-in-out;
    }

    &:hover .icon-with-stroke {
        fill: $clr-neutral-900;
        stroke: $clr-neutral-100;
    }

}

.sketches, .IA {
    @media (min-width: $breakpoint-sm) {
        .btn {
            display: none;
        }
    }

    @media (max-width: $breakpoint-sm) {
        img {
            display: none;
        }
    }
}

.btn-with-icon.left {
    @media (max-width: $breakpoint-lg) {
        justify-content: start;
        padding-left: .8rem;
    }
}