.step-header {
    background-color: $clr-neutral-900;
    color: $clr-neutral-100;
    padding: 6rem 0;
    display: grid;
    gap: .5rem;

    @media (min-width:$breakpoint-lg) {
        padding: 8rem 0;
        gap: .25rem;
        justify-items: center;
        position: relative;

        .title {
            text-transform: uppercase;
        }

        &::before,
        &::after {
            content: '';
            width: 25%;
            height: 4px;
            grid-column: 2/3;
            grid-row: 2/4;
            justify-self: start;
            background-color: $clr-neutral-100;
        }

        &::after {
            justify-self: end;
        }
        
    }
}