.two-col-grid {
    display: grid;
    gap: 1rem;
    
    @media (min-width: $breakpoint-lg) {
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
    }
}

.two-col-grid-gap-xl {
    display: grid;
    gap: 4rem;
    
    @media (min-width: $breakpoint-lg) {
        grid-template-columns: 1fr 1fr;
        gap: 4rem;
    }
}

.text-group {
    display: flex;
    flex-direction: column;
    gap: 1rem;

    @media (min-width: $breakpoint-md) {
        gap: 2rem;
    }
}