.usability-testing {
    gap: 6rem;
    @media (min-width: $breakpoint-lg) {
        gap: 8rem;
    }
}

.usability-testing__content {
    display: grid;
    gap: 4rem;
    
    @media (min-width: $breakpoint-lg) {
        gap: 6rem;
        padding: 0 4rem;
        
    }

    .card-with-img {
        @media (min-width: $breakpoint-lg) {
            display: grid;
            grid-template-columns: 1fr 1fr;
            gap: 4rem;
            align-items: center;
        }

        @media (max-width: $breakpoint-lg) {
            img {
                display: none;
            }
        }
    }

    .right {
        grid-template-columns: 1fr 1fr;
    }
}